.video__box {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 200;
  background-color: $color-default-black;

  & iframe {
    width: 100%;
    height: 100%;
  }
}

.info__video-wrapper {
  display: flex;
  flex-direction: column;
}

.video__btn {
  background: transparent;
  width: 28%;
  font-size: 12px;
  line-height: 20px;
  color: $color-sapphire;
  height: auto;
  max-width: 100px;
  max-height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 0;
  z-index: 3;
  transform: translate(-48%, -52%);

  @include vp-1199 {
    max-width: 100%;
    max-height: 100%;
    left: 50%;
    transform: translate(-52%, -50%);
  }

  @include vp-767 {
    width: 22.8%;
    height: auto;
  }

  & img {
    background: none;
  }
}
